import React from "react";
import spinner from "./images/spinner.gif";
import "./LoadingScreen.css";
const LoadingScreen = () => {
  return (
    <div className="mainLoading col-md-12">
      <img src={spinner} alt="loading..." />{" "}
      <span>
        <h1>Loading ....</h1>
      </span>
    </div>
  );
};

export default LoadingScreen;
